import { $ } from '../components';

export function renderSortMenu(e: any) {
  const { target } = e;

  if (target.closest('.wemersion')) {
    const parent = target.closest('.wemersion');

    if ($(parent).has('add')) {
      $(parent).remove('add');
      $(parent.querySelector('.wemersion__blockharmonic')).style(
        'maxHeight',
        0
      );
    } else if (!target.closest('.wemersion__blockharmonic')) {
      $(parent).add('add');
      $(parent.querySelector('.wemersion__blockharmonic')).style(
        'maxHeight',
        parent.querySelector('.wemersion__blockemersion').clientHeight
      );
    }
  } else {
    $('.wemersion').remove('add');
  }

  if (!target.closest('.searchblock')) {
    $('.searchblock__items').remove('add');
  }
  if (!target.closest('.liketuner__row')) {
    $('.liketuner__limit ul').remove('add');
    $('.liketuner__limit').remove('add');
  }
}
